body {
    font-family: 'Segoe UI', 'Arial', sans-serif;
    background: linear-gradient(135deg, #f8fafc 0%, #e0e7ff 100%);
    margin: 0;
    padding: 0;
}

h1 {
    text-align: center;
    color: #333;
}

/* Header */
.app-header {
    background: #fff;
    box-shadow: 0 4px 24px 0 rgba(20,184,166,0.10);
    padding: 2rem 0 1.5rem 0;
    border-bottom-left-radius: 32px;
    border-bottom-right-radius: 32px;
    margin-bottom: 1.5rem;
}

.header-content {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
}

.header-icon {
    font-size: 2.8rem;
    background: linear-gradient(90deg, #14b8a6 0%, #34d399 100%);
    border-radius: 50%;
    padding: 0.7rem;
    color: #fff;
    box-shadow: 0 2px 8px rgba(20,184,166,0.12);
}

.app-header h1 {
    margin: 0;
    font-size: 2.3rem;
    letter-spacing: 1.5px;
    color: #14b8a6;
    font-weight: 700;
}

.header-subtitle {
    margin: 0.2rem 0 0 0;
    font-size: 1.1rem;
    color: #134e4a;
    font-weight: 500;
    letter-spacing: 0.5px;
}

/* Footer */
.app-footer {
    background: #f3f4f6;
    color: #14b8a6;
    text-align: center;
    padding: 1.2rem 0 1rem 0;
    border-top-left-radius: 32px;
    border-top-right-radius: 32px;
    font-size: 1.05rem;
    letter-spacing: 1px;
    margin-top: 2rem;
    box-shadow: 0 -2px 12px 0 rgba(20,184,166,0.06);
}

.app-container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.itinerary-board {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 0;
}

.day-plan {
    background: linear-gradient(120deg, #fff 60%, #f3f4f6 100%);
    border: 2px solid #14b8a6;
    border-radius: 18px;
    margin: 18px 0;
    padding: 22px 28px;
    width: 340px;
    box-shadow: 0 6px 24px rgba(20, 184, 166, 0.08);
    transition: box-shadow 0.2s, transform 0.2s, opacity 0.2s;
    cursor: grab;
    font-size: 1.1rem;
}

.day-plan.dragging {
    opacity: 0.7;
    transform: scale(1.04) rotate(-2deg);
    box-shadow: 0 12px 32px rgba(20, 184, 166, 0.18);
}

.day-plan h3 {
    margin: 0 0 10px 0;
    color: #14b8a6;
    font-size: 1.3rem;
    letter-spacing: 1px;
}

.day-plan ul {
    padding-left: 18px;
    margin: 0;
}

.day-plan li {
    margin-bottom: 6px;
    color: #374151;
}

.custom-button {
    background: linear-gradient(90deg, #14b8a6 0%, #34d399 100%);
    color: #fff;
    border: none;
    border-radius: 8px;
    padding: 10px 20px;
    margin: 10px;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.2s;
}

.custom-button:hover {
    background: linear-gradient(90deg, #34d399 0%, #14b8a6 100%);
}

.drag-over {
    border: 2px dashed #34d399;
    background: #e0fdfa;
}

.modal {
    position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
    background: rgba(0,0,0,0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}
.modal-content {
    background: #fff;
    padding: 2rem;
    border-radius: 16px;
    box-shadow: 0 8px 32px rgba(99,102,241,0.18);
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.day-plan.drag-over {
    border: 2px dashed #34d399;
    background: #e0fdfa;
}
.plan-actions {
    margin-top: 10px;
    display: flex;
    gap: 10px;
}
.edit-form input {
    margin-bottom: 8px;
    padding: 6px;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 90%;
}

/* Banner */
.banner {
    background: linear-gradient(90deg, #14b8a6 0%, #34d399 100%);
    color: #fff;
    padding: 0.75rem 1.5rem;
    border-radius: 12px;
    margin-bottom: 1.2rem;
    font-size: 1.08rem;
    display: flex;
    align-items: center;
    gap: 0.7rem;
    box-shadow: 0 2px 8px rgba(99,102,241,0.10);
    justify-content: center;
}